import XHeader from '../common/header.vue'
import { Search , List} from 'vant';
import { GetOrder  } from "@/api/shop";
export default {
  name: "shopOrder",
  components:{
      XHeader, 
      Search,
      List
  },
  data() {    
    return{ 
       value:"",
       active:0,
       active1:0,
       box1:[{name:"",type:"all"},
           {name:"",type:""},
           {name:"",type:"completed"},
           {name:"",type:"closed"},
           ],
        box2:[{name:"",type:"wait_delivery"},
           {name:"",type:"wait_pay"},
           {name:"",type:"wait_received"},
           {name:"",type:"received"},
           {name:"",type:"refund"},
        ],
        fide1:false,
        orderType:"all",
        loading: false,
        finished: false,
        pages: 1,
        total_pages: 1,
        OList:[]
    };
  },
 created() {
    this.orderList()
 },
methods: {
    goBack(){
        this.$router.go(-1);
    },
    goright(){
       this.$router.push({ path: "/site"});
    },
    // onSearch(){ },
    onLoad() {
      // 
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          // 
          this.orderList();
        }
        this.loading = false;
        // 
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
    boxk(item,index){
        this.OList =[]
        this.pages = 1
       this.active = index
       if(index == 1){
            this.orderType = this.box2[this.active1].type
            this.fide1 = true
       }else{
           this.fide1 = false
            this.orderType = item.type
       }
        this.orderList();
    },
    boxk1(item,index){
        this.OList =[]
        this.pages = 1
        this.active1 = index
        this.orderType = item.type
        this.orderList();
    },
    async orderList(type){
            let res = await GetOrder({
                type:this.orderType,
                page: this.pages,
            })
            this.total_pages = res.meta.pagination.total_pages;
            this.OList =this.OList.concat(res.data)  
            console.log(res)
            this.OList.map(item=>{
                item.ordkey = 1
                //  wait_pay
                //  wait_delivery
                //  wait_received

                //  received
                //  refund
                //  completed
                //  closed
                if(item.order_status == "wait_pay"){item.ordkey =""}  
                else if(item.order_status == "wait_delivery"){item.ordkey =""}  
                else if(item.order_status == "wait_received"){item.ordkey =" "} 
                else if(item.order_status == "received"){item.ordkey =""} 
                else if(item.order_status == "refund"){item.ordkey =""} 
                else if(item.order_status == "completed") {item.ordkey =""} 
                else{item.ordkey =""} 
            })
    },
    Deliver(item){
           this.$router.push({ path: "/Logistics",query:{id:item.id,key:1}});  
    }
},
  
    
   
   
  
};
