import { WithSpec } from "@/api/user";
export default {
  name: "Datatype",
  components: {

  },
  data(){
    return{
       active:0,
       Navlist: [
           {category_name:"",id:"0",child:[],specs:[]}
       ],
       listType:null,//
       listPage:null,//
       specs:[]
    };
  },
  created(){ 
        this.WithSpecs()
  },
  methods: {
    async WithSpecs() {// 
        let route = this.$route.query;
        let res = await WithSpec({}); 
        this.Navlist = this.Navlist.concat(res.data)  //
        console.log( this.Navlist ,"@@@@@@@@@@@@@@@@@@@@@")
        console.log(res.data)
        res.data.map(item=>{
            item.children.leng = 8
            item.specs.map(type=>{
                 type.leng = 8
            })
        })
        
        for(var i = 0;i<this.Navlist.length;i++){
           if(route.key.product_name == this.Navlist[i].category_name){
                this.active = i 
           }
        }
        if(this.active == 0)this.listType = this.Navlist[1].children  //      
        else this.listType = this.Navlist[this.active].children  //
        this.listPage = this.Navlist[this.active].specs  //
        
        
        
    },
    goBack(){
         this.$router.go(-1);
    },
    Datatype(item,index){
        this.active = index; 
        if (index == 0) this.listType = this.Navlist[1].children  // 
        else  this.listType = this.Navlist[this.active].children  //
       
         this.listPage = this.Navlist[this.active].specs  //
    },
    DetaChoice(item,key,size){ // item    
            let parameter = {}
            let Choice = {}
            if(size === 0 ){//  page === 0
                Choice.catid  =  item.id  //|| ID
                item.product_name = item.category_name
                parameter.page = 0
                parameter.type = item
                parameter.Choice = Choice
                
            }else{ //  page === 1
                Choice.catid  =  this.Navlist[this.active].id  //|| ID  ID  
                Choice.specs  = item.id
                Choice.specs1 = key.id
                // specs [id[ID]] specValueId
                item.product_name = key.spec_value
                parameter.page = 1;
                parameter.type = item
                parameter.item = key  
            }
            parameter.Choice =Choice
            this.$storage.setSession('parameter', parameter)     
            this.$router.push({
                path: "/DetaChoice",
            });
    },
    Open(key,dex){//
        if(key == 'a'){
            this.listType =[...this.listType]
            this.listType.leng =this.listType.length; 
        }else{
             this.listPage =[...this.listPage]
             this.listPage[dex].leng =100
        }
    },
    Take(key,dex){
        if(key == 'a'){
           this.listType =[...this.listType]
           this.listType.leng = 8 
        }else{
            this.listPage =[...this.listPage]
            this.listPage[dex].leng =8
        }
        
    },

  }
};
