import XHeader from '../common/header.vue'
import { details } from "@/api/user";
import { List } from 'vant'
import { ordersDelete ,ordersPatch , ordersPut , queryList } from "@/api/user";
export default {
    name:"OrderDeta",
    components:{
       XHeader,
       List,
    },
    created(){ 
        let qid = this.$route.query.id;
        this.addor()
        this.comete()
    },
    data(){
        return{
            ord_problem_list:[{name:"",key:2},{name:"",key:0},{name:"",key:1},{name:"",key:0},{name:"",key:2},{name:"",key:0}] , 
            orderItem:null,
            contEmpty:true,
            Alllist:[],
            loading: false,
            finished: false,
            pages:1,
            total_pages:1,
            categoryid:"",
        }
    },
    methods:{
        goBack(){
            this.$router.go(-1);
        },
        onLoad() {
            setTimeout(() => {
                if (this.pages < this.total_pages) {
                this.pages++;
                this.comete();
                }
                this.loading = false;
                if (this.pages >= this.total_pages) {
                this.finished = true;
            }
        }, 2000);
        },
        OrderDeta(){ //
            this.$router.push({path:"/Payment"})
        },
        async comete() {// 
            let bbb =[]
            let res = await queryList({
                specs:JSON.stringify(bbb),
                recommend:"",
                order_price:"", 
                order_time:"",
                category_id:this.categoryid,
                min_price:"", 
                max_price:"",
                min_weight:"",
                max_weight:"",  
                min_inner_diameter:"", 
                max_inner_diameter:"",
                page:this.pages
            });
            console.log(res,"@@@@@@@@@@@@@@@@@@@@@@@")
            for(var i = 0; i< res.data.length ; i ++){
                for(var j=0;j<res.data[i].images.length ; j++){
                    res.data[i].images[j] = res.data[i].images[j].replace(/shop.jigesi.test/,'192.168.0.160')
                }
            }
            this.Alllist = this.Alllist.concat(res.data) 
            if( this.Alllist == ''){ this.contEmpty = false }
            this.total_pages = res.meta.pagination.total_pages
        },
         async addor(){//
            let res = await details({
                ID:this.$route.query.id,
             });
            if(res.order_status == "closed"){res.ordkey =""}  //
            else if(res.order_status == "wait_pay"){res.ordkey =""}  // 
            else if(res.order_status == "wait_delivery"){res.ordkey =" "} // 
            else if(res.order_status == "wait_received"){res.ordkey =""} //  
            else if(res.order_status == "received"){res.ordkey =""} //  
            else if(res.order_status == 'wait_change_price'){{res.ordkey =""}}
            else {res.ordkey =""} // 
            this.orderItem = res 
            console.log(res,"")
        },
        async cancel(){//
                let res = await ordersPatch({
                    ID:this.orderItem.id,
                }) 
                console.log(res,"")
                if(res){
                    res.ordkey =""
                    this.orderItem = res 
                }
            },
        async delecl(item,index){//
                let res = await ordersDelete({
                    ID:this.orderItem.id,
                })
                if(res !=""){
                   this.$router.go(-1);
                }
            },
        async Sure(item,index){//
            let res = await ordersPut({
                ID:this.orderItem.id,
            })
            if(res !=""){
                console.log(res,"")
                    this.$router.go(-1);
            }
        },
        payment(){
            let payment ={
                id:this.$route.query.id,
                price:this.orderItem.total_amount
            }
            this.$router.push({path:"/Payment",query:payment});
        }, 
        Help(item){
            this.$router.push({path:"/Help"});
        },
        Help1(dex){
           this.$router.push({path:"/Help1",query:{id:dex}});
        }
    }
}
