import XHeader from '../common/header.vue'
import { shopinform } from "@/api/shop";
import { Toast } from 'vant';
export default {
  name: "mySpread",
  components:{
      XHeader,
      Toast
  },
  data() {    
    return{ 
      mySpread_list:["","",""], //"","",""
      fromdata:{}
    };
  },
  created(){ 
      this.forem()
  },
  methods: {
    goBack(){
        this.$router.go(-1);
    },
    async forem(){
        let res = await shopinform({
            ID:this.$route.query.id
        })
        this.fromdata = res
        console.log(res,"@@@@@@@@@@@@@@@")
    },
    goright(){
        this.$router.push({ path: "/jadeType"});
    },
    mysed(item){
        console.log(item)
        if(item == ""){
            this.$router.push({ path: "/shopOrder"});
        }else if(item == ""){
             this.$router.push({ path: "/Administration"});
        }else{
            Toast('...........');
        }
    },
    Guarantee(){
       this.$router.push({ path: "/Guarantee"});
    },
    Business(item){
        this.$router.push({ path: "/Business",query:{name:item}});
    },
    Extension(){//
         this.$router.push({ path: "/Extension"});
    }
  }
};
